<script>
import headerBar from "../components/headerBar";
import footerBar from "../components/footerBar";
export default {
  data() {
    return {
      magic: false,
      magicStyleDown: "",
      magicStyleUp:""
    };
  },
  mounted() {
    let self = this;
    window.addEventListener("scroll", self.getScrollTop);
  },
  components: {
    headerBar,
    footerBar
  },
  methods: {
    goprod() {
      this.$router.push("/prod");
    },
    goprodAssistant() {
      this.$router.push("/prodAssistant");
    },
    goprodMerchants() {
      this.$router.push("/prodMerchants");
    },
    getScrollTop() {
      let self = this;
      if (!self.magic) {
        //滚动条的高度
        var scrollTop =
          document.body.scrollTop || document.documentElement.scrollTop;
        console.log(scrollTop);
        //滚动条的高度>可视区的高度
        if (!self.magic && scrollTop > 1700) {
          console.log("这里");
          self.magic = true;
          self.magicStyleDown="animated fadeInDown"
          self.magicStyleUp="animated fadeInUp"
          // self.magicStyle = "animated fadeInUp";
        }
      }
    }
  },
  destroyed() {
    //离开这个界面之后，删除，不然会有问题
    let self = this;
    window.removeEventListener("scroll", self.getScrollTop);
  }
};
</script>
